/* PageTitle */
.page-title {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
  grid-column: auto/span 2;
  color: white;
  font-size: 35px;
  font-weight: 800;
  box-shadow: none;
}


.thumb-2x1 {
  grid-column: auto/span 2;
  grid-row: auto/span 1;
}

.thumb-2x2 {
  height: 320px;
  grid-column: auto/span 2;
  grid-row: auto/span 2;
}

.thumb-2x2 > .new-badge {
  display: flex;
  position: absolute;
  width: 110px;
  height: 110px;
  transform: translate(-8px, -8px);
  z-index: 3;
  background-repeat: no-repeat;
  background-size: cover;
}



.thumb-2x1 > .new-badge {
  display: flex;
  position: absolute;
  width: 55px;
  height: 55px;
  transform: translate(-4px, -4px);
  z-index: 3;
  background-repeat: no-repeat;
  background-size: cover;
}



.thumb-2x2:hover > .new-badge {
  display: none;
}
.thumb-2x1:hover > .new-badge {
  display: none;
}
.container {
  width: 100%;
  align-content: center;
  justify-content: right;
  margin-top: var(--Top-Padding);
  /* padding-left: 76px; */
 
}




.empty-thumb::before {
  height: 130px;
  content: "";
  display: block;
  grid-column-start: span 2;
}

.thumb-container {
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.thumb-img {
  width: 100%;
  height: 100%;
  -webkit-transition: all 200ms ease-in;
  transition: all 200ms ease-in;
  -ms-transition: all 200ms ease-in;
  -moz-transition: all 200ms ease-in;
}

.thumb-container:hover > .thumb-img {
  -webkit-transition: all 100ms ease-in;
  -webkit-transform: scale(1.2);
  -ms-transition: all 100ms ease-in;
  -ms-transform: scale(1.2);
  -moz-transition: all 100ms ease-in;
  -moz-transform: scale(1.2);
  transition: all 100ms ease-in;
  transform: scale(1.2);
}
.banner_image {
  width: 100%;
  height: 87%;
  background-size: cover;
  background-color: hotpink;
  overflow: hidden;
  background-repeat: no-repeat;
  -webkit-transition: all 200ms ease-in;
  -ms-transition: all 200ms ease-in;
  -moz-transition: all 200ms ease-in;
  transition: all 200ms ease-in;
}
.banner_title {
  width: 100%;
  height: 13%;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  text-align: center;
  color: black;
  font-size: 20px;
  font-weight: 800;
  position: absolute;
  background-color: var(--white);
  bottom: 0;
}

.banner_flex {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-content: center;
  cursor: pointer;
}

.title {
  display: flex;
  align-items: flex-end;
  position: absolute;
  align-content: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 105%;
  color: var(--white);
  opacity: 0;
  font-size: 14px;
  font-weight: 700;
  text-shadow: 0px 1px 1px #000000;
}


.thumb-2x1:hover > .thumb-container > .title {
  z-index: 2;
  opacity: 1;
  height: 95%;
  transition: height 0.1s;
}

.thumb-2x2:hover > .thumb-container > .title {
  z-index: 2;
  opacity: 1;
  height: 95%;
  transition: height 0.1s;
  font-size: 20px;
}



.thumb-2x1:hover {
  z-index: 3;
  -webkit-transition: all 100ms ease-in;
  -webkit-transform: scale(1);
  -ms-transition: all 100ms ease-in;
  -ms-transform: scale(1);
  -moz-transition: all 100ms ease-in;
  -moz-transform: scale(1);
  transition: all 100ms ease-in;
  transform: scale(1);
}

.thumb-2x2:hover {
  z-index: 3;
  -webkit-transition: all 100ms ease-in;
  -webkit-transform: scale(1.05);
  -ms-transition: all 200ms ease-in;
  -ms-transform: scale(1.05);
  -moz-transition: all 200ms ease-in;
  -moz-transform: scale(1.05);
  transition: all 200ms ease-in;
  transform: scale(1.05);
}




.thumb-2x1:hover > .thumb-container > .cover {
  z-index: 1;
  opacity: 1;
  transition: opacity 0.2s;
}

.thumb-2x2:hover > .thumb-container > .cover {
  z-index: 1;
  opacity: 1;
  transition: opacity 0.2s;
}


